header { 
  height: 50px;
  text-align: center;
  color: rgb(240, 244, 239);
  background-color: rgb(52, 73, 102);
}

html {
  max-width: 900px;
  margin: auto;
}


/*///////////////////////////*/
/* SHARED STYLING ACROSS APP */
/*///////////////////////////*/
.bold {
  font-weight: bold;
}

.font75{
  font-size: 75%;
}

.font150{
  font-size: 150%;
}

.font125{
  font-size: 125%;
}

body {
  color: rgb(13, 24, 33);
}

@media only screen and (min-width: 625px) {   
  .accordian {
    margin-right: 5rem;
    margin-left: 5rem;
  }
  li {
    font-size: 125%;
  }
  .testimonialsContainer p {
    font-size: 150%;
  }
  .aboutContainer {
    font-size: 125%;
  }
}
