.residentialContainer { 
    min-height: 200px;
    background-color: rgb(178, 192, 205);
    padding: 1rem;
}

.formDiv {
    text-align: center;
    display: flex;
    align-items: center;
}

.flexLabel {
    flex-basis: 30%;
    text-align: right;
    margin-right: 1rem;
    margin-bottom: 2rem;
}

.flexTextbox {
    flex-basis: 70%;
    margin-right: 1rem;
    margin-bottom: 2rem;
}

.formCheckbox {
    text-align: center;
    display: flex;
    align-content: flex-start;
    margin-bottom: 1rem;
}

.flexCheckbox {
    flex-basis: 50%;
    align-self: flex-start;
}

.submit {
    text-align: center;
}

