.weatherContainer { 
    height: 165px;
    padding: .5rem;
    display: flex;
    justify-content: space-evenly;
    background-color: rgb(52, 73, 102);
}

.weatherCard {
    width: ((100% / 3) -4px);
    border: 1px solid black;
    text-align: center;
    border-radius: 25%;
    min-width: 125px;
    padding-right: .5rem;
    padding-left: .5rem;
    padding-top: 1rem;
    padding-bottom: 1rem;
    background-color: rgb(178, 192, 205);
    background-color: rgb(240, 244, 239);
}

.maxTemp{
    font-size: 125%;
    /* flex-basis: 50%; */
}

.icon {
    width: 50%;
    /* flex-basis: 50%; */
}

p { 
    margin-bottom: 0px;
}


